import React, { useState, useEffect, useContext } from 'react';
import { TextField, Button, Box, Typography, List, ListItem, ListItemText, ImageList, ImageListItemBar, ImageListItem } from '@mui/material';
import getAllProducts from '../netlify/getAllProducts';
import addProduct from '../netlify/addProduct';
import updateProduct from '../netlify/updateProduct';
import AuthContext from '../AuthContext';

const AddProductForm = () => {
  const [product, setProduct] = useState({
    name: '',
    description: '',
    price: '',
    image_url: '',
  });
  const [products, setProducts] = useState([]);
  const { authToken } = useContext(AuthContext);
  

  useEffect(() => {
    if (!authToken) {
      throw new Error('No authentication token available');
    }

    const fetchProducts = async () => {
      const fetchedProducts = await getAllProducts();
      setProducts(fetchedProducts.products);
      
    };

    fetchProducts();
  }, [authToken]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProduct({ ...product, [name]: value });
  };

  const handleSubmit = async (e) => {
    if (!authToken) {
      throw new Error('No authentication token available');
    }

    e.preventDefault();
    if (product.id) {
      await updateProduct(product);
    } else {
      await addProduct(product);
    }

    setProduct({ name: '', description: '', price: '', image_url: '' }); // Reset form
    const fetchedProducts = await getAllProducts(); 
    setProducts(fetchedProducts.products);
  };

  const handleEdit = (product) => {
    if (!authToken) {
      throw new Error('No authentication token available');
    }
    setProduct(product);
  };

  if (!authToken) {
    return <p>You are not logged in.</p>;
  }
  const defaultImage = 'data:image/png;base64,'; // Default image URL (you can use any default image URL)

  return (
    <Box component="form" onSubmit={handleSubmit} sx={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      width: '100%', // Make sure the Box takes full width on mobile
      '& > :not(style)': { m: 1, width: '75ch' }, // Default width
      '@media (max-width: 600px)': {
        '& > :not(style)': { width: '90%' }, // Change width for smaller screens (mobile)
      },
    }}>
      <Typography variant="h4" gutterBottom>
        {product.id ? 'Edit Product' : 'Add New Product'}
      </Typography>
      <TextField
        fullWidth
        margin="normal"
        label="Product Name"
        name="name"
        value={product.name}
        onChange={handleChange}
        required
      />
      <TextField
        fullWidth
        margin="normal"
        label="Description"
        name="description"
        value={product.description}
        onChange={handleChange}
        required
        multiline
        rows={4}
      />
      <TextField
        fullWidth
        margin="normal"
        label="Price"
        name="price"
        type="number"
        value={product.price}
        onChange={handleChange}
        required
      />
      <TextField
        fullWidth
        margin="normal"
        label="Image URL"
        name="image_url"
        value={product.image_url}
        onChange={handleChange}
        required
      />
      <Button variant="contained" color="primary" type="submit" sx={{ mt: 2 }}>
        {product.id ? 'Update Product' : 'Add Product'}
      </Button>

      <Typography variant="h6" gutterBottom sx={{ mt: 4 }}>
        Product List
      </Typography>
      <List>
      {Array.isArray(products) && products.length > 0 ? (
      <ImageList cols={3} rowHeight={200} gap={16}>
        {products.map((prod) => (
          <ImageListItem key={prod.id} onClick={() => handleEdit(prod)}>
            <img
              src={prod.image_url || defaultImage}
              alt={prod.name}
              style={{
            objectFit: 'contain', // Ensure the entire image is visible without distortion
            maxWidth: '100%',     // Ensure image doesn't overflow the container
            maxHeight: 200,       // Set a maximum height for the image
            width: 'auto',        // Maintain the image's original aspect ratio
            height: 'auto',       // Maintain the image's original aspect ratio
          }}
            />
            <ImageListItemBar
              title={prod.name}
              subtitle={`Price: ${prod.price} INR`}
              position="bottom"
              style={{ background: 'rgba(0, 0, 0, 0.5)' }} // Optional: for better visibility
            />
          </ImageListItem>
        ))}
      </ImageList>
    ) : (
      <Typography>No products available.</Typography> // Show a fallback message if no products
    )}
      </List>
    </Box>
  );
};

export default AddProductForm;
